import React from 'react';
import Main from '../components/section/Main';

const Live = () => {
    // URL에서 선택된 역 이름 가져오기
    const searchParams = new URLSearchParams(window.location.search);
    const stationName = searchParams.get('stationName');

    return (
        <Main
            title="상행하행"
            description="테스트 페이지"
        >
            {/* 역 이름을 표시하는 텍스트 요소 */}
            <div>선택된 역 이름: {stationName}</div>
        </Main>
    );
};

export default Live;